// Company

.company {
	padding: 0 0 62px;
	h2 {
		text-transform: uppercase;
		margin: 0 0 20px; }
	p {
		margin: 0 0 20px; }
	em {
		max-width: 918px; }

	@include rmin($tablet) {
		h2 {
			margin: 0 0 28px; }
		p {
			margin: 0 0 24px; }
		em {
			margin: 0 0 34px; } } }



// -----

.certificates {
	padding: 0 0 6px;
	@include rmin($tablet) {
		padding: 32px 0 0; } }

.certificates__grid {
	margin: 0 -20px;
	padding: 5px 0 60px;
	.row {
		margin: 0 -10px;
		& > [class^="col"] {
			padding: 0 10px;
			position: relative;
			&:nth-child(2n) {
				top: 60px; } } }
	@include rmin($tablet) {
		margin: 0 0 0 -7.4%;
		padding: 0 0 0px 10px;
		.row {
			margin: 0 -20px;
			& > [class^="col"] {
				padding: 0 20px;
				position: relative;
				&:nth-child(2n) {
					top: 0; }
				&:nth-child(2) {
					top: 165px; }
				&:nth-child(5) {
					top: 120px; }
				&:nth-child(3n) {
					top: 82px; }
				&:nth-child(7) {
					top: -30px; } } } }
	@include rmin($desktop) {
		margin: 0;
		padding: 0 0 0px;
		margin-bottom: -5px;
		max-width: 1040px; } }

.certificates__item {
	display: block;
	margin: 0 0 20px;
	border: solid 5px #f2f1ed;
	img {
		display: block;
		width: 100%; }
	@include rmin($tablet) {
		margin: 0 0 50px; } }

// -----

.clients {
	padding: 0 0 15px;
	@include rmin($tablet) {
		padding: 0 0 10px;
		p {
			padding-right: 50px; } }
	@include rmin($desktop) {
		padding: 0 0 5px; } }


.clients__grid {
	margin: 0 -10px 9px;
	.row {
		margin: 0 -20px;
		align-items: center;
		& > [class^="col"] {
			padding: 0 20px;
			position: relative; } }
	@include r($mobile) {
		.col-6 {
			&:nth-child(1) {
				order: 1;
				.clients__item {
					padding: 0;
					max-width: 150px;
					transform: translateY(-10px); } }
			&:nth-child(2) {
				order: 2;
				.clients__item {
					transform: translateY(-20px); } }
			&:nth-child(3) {
				order: 8;
				.clients__item {
					max-width: 150px; } }
			&:nth-child(4) {
				order: 3;
				.clients__item {
					transform: translateY(-15px); } }
			&:nth-child(5) {
				order: 4;
				.clients__item {
					max-width: 150px;
					transform: translateY(-15px); } }
			&:nth-child(6) {
				order: 10;
				.clients__item {
					transform: translateY(-20px); } }
			&:nth-child(7) {
				order: 5;
				.clients__item {
					max-width: 150px;
					transform: translateY(-15px); } }
			&:nth-child(8) {
				order: 6;
				.clients__item {
					transform: translateY(-20px); } }
			&:nth-child(9) {
				order: 9;
				.clients__item {
					max-width: 150px;
					transform: translateY(-30px); } }
			&:nth-child(10) {
				order: 7;
				.clients__item {
					transform: translateY(-10px); } } } }

	@include rmin($tablet) {
		margin: 0 0 0 -7.4%;
		padding: 22px 0 0 10px; }
	@include rmin($desktop) {
		margin: 0;
		padding: 0px 0 0;
		margin-top: -10px;
		max-width: 1160px; } }


.clients__item {
	display: block;
	text-align: center;
	margin: 0 auto;
	padding: 10px 0;
	max-width: 130px;
	img {
		display: inline-block;
		visibility: middle; }
	@include rmin($tablet) {
		max-width: none;
		margin: 0 0 48px; } }

// -----

.profession {
	margin: 0 -10px 0 0;
	padding: 0 0 34px;
	@include rmin($tablet) {
		margin: 0;
		padding: 0 0 45px;
		p {
			margin-right: 0%; } }
	@include rmin($desktop) {
		padding: 7px 0 48px;
		p {
			margin-right: -10%; } } }

.profession__image {
	margin: 0 -20px 20px 0;
	@include rmin($tablet) {
		margin: 10px -20px 20px 0; }
	@include rmin($desktop) {
		margin: 5px -20px 20px 50px; } }


// -----

.team {
	@include rmin($tablet) {
		h2 {
			margin: 0 0 22px; }
		p {
			margin-right: -5%; } }
	@include rmin($desktop) {
		p {
			margin-right: 0; } } }


.team__grid {
	margin: 0 -20px;
	padding: 2px 0 60px;
	.row {
		margin: 0 -10px;
		& > [class^="col"] {
			padding: 0 10px;
			position: relative;
			&:nth-child(odd) {
				top: 60px; } } }
	@include rmin($tablet) {
		margin: 0 0 0 -7.4%;
		padding: 5px 0 168px 10px;

		.row {
			margin: 0 -20px;
			& > [class^="col"] {
				padding: 0 20px;
				position: relative;
				&:nth-child(2n) {
					top: 0; }
				&:nth-child(3n-2) {
					top: 160px; }
				&:nth-child(3n) {
					top: 80px; } } } }
	@include rmin($desktop) {
		margin: 0;
		padding: 10px 0 180px 10px;
		max-width: 1040px; } }

.team__item {
	display: block;
	margin: 0 0 22px;
	img {
		display: block;
		width: 100%; }
	@include rmin($tablet) {
		margin: 0 0 38px; } }
