.typography br {
  line-height: 42px;
}

.list__ul, .typography ul {
  padding: 0 0 0 55px;
  margin: 0 0 25px;
  list-style: none;
}

@media only screen and (min-width: 768px) {
  .list__ul, .typography ul {
    padding: 0 0 0 37px;
  }
