h1 {
  font-size: 30px;
  line-height: 1;
  font-weight: 900;
  margin: 0 0 18px;
}

@media only screen and (min-width: 768px) {
  h1 {
    font-size: 50px;
    margin: 0 0 24px;
  }
}

h2 {
  font-size: 20px;
  line-height: 25px;
  font-weight: 900;
  margin: 0 0 27px;
