@include rmin(1441) {
	.requisites__container {
		max-width: 87%; }
	.team__grid, .certificates__grid {
		max-width: 81%; }
	.clients__grid {
		max-width: 90.5%; }
	.company em {
		max-width: 70%; } }

.company {
	.container {
		max-width: 100%; } }

.results {
	.container {
		max-width: 100%; } }

.error {
	.container {
		max-width: 100%; } }

// .categories,
