body, html {
    width: 100%;
    background: white;
    color: $fontColor;
    min-width: 320px;
    max-width: 100%;
    margin: 0;
    overflow-x: hidden;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale; }

body *::selection {
  background-color: $primaryColor;
  color: white; }

.page {
    position: relative;
    overflow: hidden;
    width: 100%;
    // padding: 100px 0 0
    padding: 102px 0 0;
    @include rmin($tablet) {
        padding: 157px 0 0; }
    @include rmin($desktop) {
        padding: 188px 0 0; }
    &__content {
        background: white;
 } }        // flex: 1 1 auto

.container {
    max-width: 1440px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 30px;
    padding-right: 30px;
    @include rmin($tablet) {
      padding-left: 20px;
      padding-right: 20px; } }

.col-indent {
    @include rmin($tablet) {
        // padding: 0 0 0 7.2%
        // padding-left: 55px
        padding-left: 7.4%; }
    @include rmin($desktop) {
        // padding-left: 132px
        // padding: 0 0 0 9.2%
        padding-left: 9.6%;
        // padding: 0 0 0 9.6%
 } }        // padding: 0 0 0 138px

h2 {
    @include rmin($tablet) {
 }        // text-indent: 4px
    @include rmin($desktop) {
        text-indent: 0; } }

.col-xl-20 {
    @include rmin($xl) {
        width: 20%;
        flex: 0 0 20%;
        max-width: 20%; } }

.col-xl-40 {
    @include rmin($xl) {
        width: 40%;
        flex: 0 0 40%;
        max-width: 40%; } }

.dev-bp-debug {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 2299;
  background: black;
  padding: 5px;
  font-size: 12px;
  color: white;
  transition: .2s; }

