// Sku

.sku {
	margin: 0 -20px 0 0;
	@include rmin($tablet) {
		margin: 0;
 } }		// padding: 46px 0 0

// .sku__head, .sku__main
/// +rmin($tablet)
/// 	padding: 0 0 0 7.2%
/// +rmin($desktop)
/// 	padding: 0 0 0 9.2%

.sku-info {
	padding-left: 6%;
	padding-right: 6%;
	&__top {
		display: flex; }
	&__text {
		.col-lg-6 {
			@include rmin($desktop) {
				max-width: 43%;
				flex-basis: 43%;
				&:nth-child(even) {
					margin-left: 11.5%; } } } } }

.sku__price {
	display: block;
	font-size: 30px;
	line-height: 40px;
	font-weight: 400;
	margin: 0 0 47px;
	white-space: nowrap;
	@include rmin($tablet) {
		font-size: 50px;
		line-height: 60px;
		margin: 0 0 70px; } }


.is-sku-open {
	.sku__action-btn {
		width: 40px;
		color: transparent;
		background: #999999;
		&:hover {
			background: $primaryColor; }

		@include rmin($tablet) {
			width: 60px; }

		.ico {
			opacity: 1; } }


	.sku__action-form {
		opacity: 1;
		visibility: visible;
		transform: scale(1); } }

.sku__action {
	margin: 0 0 47px auto;
	float: right;
	position: relative;
	@include rmin($tablet) {
		margin: 0 0 60px 19%; } }


.sku__action-btn {
	font: 900 15px/20px $fontBase;
	padding: 10px 20px;
	position: relative;
	z-index: 110;
	overflow: hidden;
	width: 135px;
	transition: 0.3s $ease;
	background: $primaryColor;
	color: #fff;
	cursor: pointer;
	&:hover {
		box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25); }
	&:active {
		transition: none;
		box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.25); }
	.ico {
		position: absolute;
		top: 50%;
		left: 50%;
		font-size: 16px;
		transform: translate(-50%,-50%);
		margin: 0;
		display: block;
		transition: inherit;
		opacity: 0; }
	@include rmin($tablet) {
		font-size: 25px;
		line-height: 30px;
		padding: 15px 30px;
		width: 200px;
		.ico {
			font-size: 24px; } } }


.sku__action-form {
	position: absolute;
	top: 0;
	right: 0;
	padding: 28px 10px 60px 30px;
	background-color: #ffffff;
	box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25);
	z-index: 100;
	max-width: 650px;
	width: calc(100vw - 40px);
	transition: 0.4s $ease;
	transform: scale(0);
	transform-origin: 100% 0;
	opacity: 0;
	visibility: hidden;
	.ui__legend {
		margin: 0 0 22px; }
	.ui__caption {
		// fix for floating (dynamic labels)
		margin-bottom: 50px; }
	@include rmin($tablet) {
		padding: 56px 60px;
		.btn {
			float: right; } } }



.sku__caption {
	font-size: 20px;
	line-height: 25px;
	padding: 0 0 70px;
	margin: 0;
	p {
		margin: 0 0 20px;
		&:last-child {
			margin: 0; } }
	@include rmin($tablet) {
		font-size: 25px;
		line-height: 35px;
		margin: 0 -20px 0 0; } }



.sku__chars {
	font-size: 15px;
	line-height: 20px;
	margin: 0 0 50px;
	h2, h3 {
		text-transform: uppercase;
		font-size: 20px;
		line-height: 1;
		font-weight: 900;
		margin: 0 0 20px; }
	p {
		margin: 0 0 20px; }
	@include rmin($tablet) {
		font-size: 18px;
		line-height: 25px;
		margin: 0 0 78px;
		h2, h3 {
			margin: 0 0 20px;
			font-size: 25px; }
		p {
			margin: 0 0 25px; }
 }		// margin: 0 0 40px
	.col-md-6 {
		@include rmin($tablet) {
			max-width: 45%;
			flex-basis: 45%; } } }

.sku__info {
	font-size: 15px;
	line-height: 20px;
	margin: 6px 0 50px;
	h2, h3 {
		text-transform: uppercase;
		font-size: 20px;
		line-height: 1;
		font-weight: 900;
		margin: 0 0 25px; }
	p {
		margin: 0 0 20px; }
	@include rmin($tablet) {
		font-size: 18px;
		line-height: 25px;
		margin: 6px 0 103px;
		h2, h3 {
			margin: 0 0 32px;
			font-size: 25px; }
		p {
			margin: 0 0 25px; }
 } }		// margin: 0 0 40px

.sku__info-list {
	margin: 0;
	padding: 0;
	list-style: none;


	& > li {
		position: relative;
		z-index: 10;
		padding: 8px 0;
		display: flex;
		&:after {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 10px;
			height: 10px;
			background: #f2f1ed;
			z-index: -1; } }

	b {
		font-weight: 900;
		margin: 0 0 0 auto; } }
