.catalog__main {
	position: relative; }

.catalog__wrapper {
	&:after {
		content: '';
		display: table;
		width: 100%;
		clear: both; } }

.catalog__content {
	position: relative;
	z-index: 1;
	padding: 30px 0 60px;
	width: 100%;
	float: left;
	transition: 0.3s $ease;
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		right: -30px;
		bottom: 0;
		min-width: 120vw;
		background: #f2f1ed;
		z-index: -1; }

	@include rmin($tablet) {
		padding-bottom: 80px; }

	@include rmin($desktop) {
		padding: 30px 24.2% 80px 9.8%;
		margin: 0 0 0 -20px;
		transform: translate3d(0,0,0);
		&:after {
			right: 220px; } } }


.catalog__filter {
	position: relative;
	left: 30px;
	z-index: 80;
	padding: 0 0 20px 20px;
	transition: 0.3s $ease;
	float: left;
	width: 200px;
	margin: 0 0 0 -200px;
	opacity: 0;
	visibility: hidden;
	transform: translate3d(200px,0,0);
	@include rmin($tablet) {
		left: 20px;
		width: 275px;
		padding: 0 0 40px 40px;
		margin: 0 0 0 -275px;
		transform: translate3d(275px,0,0); }
	@include rmin($desktop) {
		left: 0;
		width: 220px;
		margin: 0 0 0 -220px;
		opacity: 1;
		visibility: visible;
		transform: translate3d(0,0,0); }

	&-hidden {
		display: none; } }


.is-filter-open {
	.btn--filter {
		.ico-filter2 {
			opacity: 0;
			transform: scale(0); }
		.ico-close {
			opacity: 1;
			transform: translate(-50%,-50%) scale(1); } }
	.catalog__filter {
		opacity: 1;
		visibility: visible;
		transform: translate3d(0,0,0); }
	.catalog__content {
		transform: translate3d(-200px,0,0);
		@include rmin($tablet) {
			transform: translate3d(-275px,0,0); }
		@include rmin($desktop) {
			transform: translate3d(0,0,0); } } }


.catalog__filter-checkboxes {
	padding: 0 0 30px;
	.ui__check {
		margin: 0 0 15px;
		&:last-child {
			margin: 0; } }
	@include rmin($tablet) {
		padding: 0 0 40px;
		.ui__check {
			margin: 0 0 20px; } } }

.catalog__filter-subsections {
	margin: 0 0 0 -10px;
	.ui__check {
		margin: 0; }
	@include rmin($tablet) {
		margin: 0 0 0 -20px; } }

.catalog__filter-all {
	padding: 0 0 30px;
	@include rmin($tablet) {
		padding: 0 0 40px; } }

.catalog__filter-action {
	padding: 0 0 52px;
	@include rmin($tablet) {
		padding: 0 0 66px;
		margin: 0 0 0 -20px; } }

// -----

.catalog__categories {
	background: #fff;
	box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25);
	position: absolute;
	top: -125px;
	right: -30px;
	z-index: 100;
	padding: 80px 10px;
	transition: 0.3s $ease;
	transform: scale(0);
	opacity: 0;
	visibility: hidden;
	transform-origin: 100% 0;
	width: 100vw;
	max-width: 500px;
	@include rmin($tablet) {
		top: -165px;
		right: 0;
		padding: 60px 90px 60px 20px; }
	@include rmin($desktop) {
		padding: 60px 60px 60px 40px;
		width: 500px;
		top: 105px;
		right: auto;
		left: 50%;
		margin: 0 0 0 -50vw;
		transform-origin: 0 0; }
	.categories__grid {
		margin: 0; }
	.categories__item {
		font-size: 15px;
		line-height: 20px;
		margin: 0 0 22px 18px;
		&:hover {
			.categories__item-image:after {
				left: -20px; }
			.categories__item-front, .categories__item-back {
				transform: translate(-10px,0); } } }

	.categories__item-image {
		height: 105px;
		&:after {
			left: 44px; }
		img {
			max-height: 75px; } }

	.categories__item-front, .categories__item-back {
		transform: translate(-40px,0); } }


.is-categories-open, .is-categories-open-fixed {

	.catalog__categories {
		transform: scale(1);
		opacity: 1;
		visibility: visible; } }

.is-categories-open-fixed {
	.btn--categories {
		.ico-categories {
			opacity: 0;
			transform: scale(0); }
		.ico-close {
			opacity: 1;
			transform: translate(-50%,-50%) scale(1); } } }

// -----

.catalog__ctrl {
	margin: 0 -30px;
	padding: 0 10px;
	.row {
		margin: 0 -10px;
		& > [class^="col"] {
			padding: 0 10px; } }

	@include rmin($tablet) {
		margin: 0;
		padding: 0;
		.row {
			margin: 0 -20px;
			& > [class^="col"] {
				padding: 0 20px; } } } }

.catalog__sorting {
	position: relative;
	margin: 0 0 30px;
	display: inline-block;
	@include rmin($tablet) {
		margin: 0 0 40px; } }

.catalog__sorting-btn {
	display: block;
	font: 900 12px/1 $fontBase;
	text-transform: lowercase;
	padding: 8px 0;
	border-bottom: 3px solid #000;
	color: #000;
	background: none;
	cursor: pointer;
	transition: .3s $ease;
	mark {
		display: none;
		background: none; }
	&:hover {
		color: $primaryColor; }
	@include rmin($tablet) {
		font-size: 15px;
		mark {
			display: inline; } } }

.catalog__sorting-dropdown {
	position: absolute;
	top: -30px;
	left: -10px;
	right: -10px;
	visibility: hidden;
	opacity: 0;
	transition: 0.3s $ease;
	background: #fff;
	box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25);
	z-index: 100;
	padding: 30px 10px;
	min-width: 225px;
	.ui__check {
		padding: 0;
		margin: 0; }
	@include rmin($tablet) {
		left: -40px;
		right: -40px;
		padding: 30px 20px; } }

.catalog__sorting-text {
	display: block;
	font: 900 12px/15px $fontBase;
	text-transform: lowercase;
	padding: 8px 0;
	display: none;
	@include rmin($tablet) {
		display: block;
		font-size: 15px;
		padding: 8px 20px;
		margin: 0 0 20px; } }


.is-sorting-open {
	.catalog__sorting-dropdown {
		opacity: 1;
		visibility: visible; } }

.catalog__display {
	display: flex;
	.ui__check {
		margin: 0 10px 0 0; } }

// -----



.catalog__grid {
	margin: 0 -30px;
	padding: 0 10px;
	.row {
		margin: 0 -10px;
		& > [class^="col"] {
			padding: 0 10px; } }
	@include rmin($tablet) {
		margin: 0;
		padding: 0;
		.row {
			margin: 0 -20px;
			& > [class^="col"] {
				padding: 0 20px; } } } }


.catalog__load {
	padding: 34px 0 0;
	@include rmin($tablet) {
		padding: 34px 20px 0 7.2%;
		margin: 0 -20px; }
	@include rmin($desktop) {
		margin: 0;
		padding: 30px 0 0;
 } }		// posible conflict with other page
