// Promo

.promo {
	position: relative;
	margin: -40px 0 45px;
	.container {
 }		// max-width: 1920px
	.row {
		margin: 0;
		z-index: 10;
		position: relative;
		& > [class^="col"] {
			padding: 0;
			&:nth-child(2) {
				&:after {
					content: '';
					display: block;
					position: absolute;
					top: 0;
					left: 0;
					bottom: 0;
					width: 100vw;
					background: #f6f6f6;
					z-index: -1; } } } }
	@include rmin($tablet) {
		margin: -40px 0 43px; }

	@include rmin($desktop) {
		margin-top: -50px;
		margin-bottom: 0; } }
