// Seo

.seo {
	padding: 80px 0 0;
	font-size: 15px;
	line-height: 20px;
	@include rmin($tablet) {
		padding: 115px 0 20px;
		font-size: 18px;
		line-height: 25px; }
	@include rmin($desktop);

	&__row {
		.col-lg-6 {
			&:nth-child(1) {
				padding-right: 5%;
				display: flex;
				flex-direction: column;
				align-items: flex-start; }

			&:nth-child(2) {
				margin-left: -5%;
				flex-basis: 55%;
				max-width: 55%; } } } }

.seo__heading {
	font-size: 20px;
	line-height: 25px;
	font-weight: 900;
	margin: 0 0 18px;
	text-transform: uppercase;
	@include rmin($tablet) {
		font-size: 25px;
		line-height: 1;
		margin: 0 0 26px; } }

.seo__collapse {
	flex: 1 1 auto;
	display: flex;
	flex-direction: column;
	position: relative;
	max-height: 240px;
	overflow: hidden;
	transition: 0.4s $ease;
	margin: 0 0 0px;
	&:after {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		bottom: -15px;
		height: 100px;
		width: 100%;
		background: linear-gradient(to bottom, rgba(255,255,255,0.90) 3%,rgba(255,255,255,0.99) 100%);
		transition: inherit; }
	p {
		margin: 0 0 25px;
		&:last-child {
			margin: 0; } }
	img {
		max-width: 100%; }
	& + .btn {
		transform: translateY(16px); }
	&[aria-expanded="true"] {
		&:after {
			height: 0; }
		& + .btn {
 } } }			// transform: translateY(-0px)


.seo__image {
	display: none;
	margin: 0;
	img {
		display: block;
		width: 105%; }
	@include rmin($desktop) {
		display: block; } }
