	// display: inline-block
	// vertical-align: top
	// font-size: 15px
	// line-height: 1
	// font-weight: 900
	// color: #252525
	// padding: 6px 0 8px
	// border-bottom: 3px solid #252525
	// margin: 20px 0
	// transition: 0.3s
	position: absolute;
	// top: 100%
	left: 0;
	right: 0;
	bottom: -65px;
	padding: 15px 0;
	// &:hover
	/// color: $primaryColor

	@include rmin($mobile) {
 }		// left: 8.333%

	@include rmin($tablet) {
		// font-size: 20px
		padding: 42px 0;
		// left: 4px
		padding-left: 7.2%;
		top: auto;
		bottom: -127px; }

	@include rmin($desktop) {
		position: relative;
		padding-left: 7.2%;
		top: 0;
		bottom: auto;
		left: 0;
		// padding: 106px 20px 55px 20px
		// padding: 106px 0 55px 19.333%
		padding: 106px 0 106px 19.333%;
