// -----

.news__sticky {
	margin: 0 -30px;
	transform: translate3d(0,0,0);
	@include rmin($desktop) {
		margin: 0 -20px; } }

.js-sticky {
	&.is-fixed {
		transform: translate3d(0,0,0);
		z-index: 2; } }


