// buttons
button {
    -webkit-apperance: none;
    border: 0;
    box-shadow: none;
    &:focus {
        outline: none; } }

.btn {
    display: inline-block;
    vertical-align: top;
    position: relative;
    text-align: center;
    font: 900 25px/30px $fontBase;
    padding: 15px 30px;
    transition: 0.3s $ease;
    border: none;
    cursor: pointer;
    &:focus, &:active {
        outline: none; }

    &--block {
        display: block;
        width: 100%; }
    @include rmin($tablet); }

.btn--link {
    background: none;
    font-size: 15px;
    line-height: 1;
    padding: 0 0 8px;
    border-bottom: 3px solid #000;
    &:hover {
        color: $primaryColor; } }

.btn--collapse {
    font-size: 15px;
    line-height: 1;
    padding: 0 0 7px;
    border-bottom: 3px solid #000;
    @include rmin($tablet) {
        font-size: 20px; }
    @include rmin($desktop) {
        font-size: 25px; }
    &:hover {
        color: $primaryColor; } }

.btn--small {
    padding: 8px 10px 7px;
    font-size: 15px;
    line-height: 1; }

.btn--large {
    // font-size: 25px
 }    // line-height: 30px


.btn--grey {
    background: #999999;
    color: #fff;
    &:hover {
        background: $primaryColor;
        color: #fff; } }

.btn--red {
    background: $primaryColor;
    color: #fff;
    &:hover {
        box-shadow: 0 20px 60px 0 rgba(0, 0, 0, 0.25); }
    &:active {
        transition: none;
        box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.25); } }

.btn--load {
    font-size: 15px;
    line-height: 1;
    color: #252525;
    padding: 8px 0 5px;
    overflow: hidden;
    &:after, &:before {
        content: '';
        display: block;
        position: absolute;
        bottom: 0px;
        left: 0;
        width: 100%;
        height: 3px;
        transition: 0.3s $ease; }
    &:after {
        background: #252525;
        z-index: 1; }
    &:before {
        background: $primaryColor;
        z-index: 2;
        transform: translate(0,0);
        width: 0; }
    &:hover {
        color: $primaryColor; }
    &.is-loading {
      &::before {
        animation: loadBtn .75s $ease infinite;
        animation-direction: both; } }

    @include rmin($tablet) {
      font-size: 20px; }
    @include rmin($wide) {
      padding: 8px 0 11px;
      font-size: 25px; } }

@keyframes loadBtn {
  from {
    width: 0;
    transform: translate(0%,0); }
  70% {
    width: 200%;
    transform: translate(0%,0); }
  100% {
    width: 200%;
    transform: translate(200%,0); } }

.btn--chat {
    border: none;
    position: fixed;
    bottom: 0;
    right: 10px;
    z-index: 200;
    text-transform: lowercase;
    font-size: 15px;
    line-height: 20px;
    padding: 5px 20px;
    width: 160px;
    background: #999999;
    color: #fff;
    &:hover {
        background: $primaryColor; }

    @include rmin($tablet) {
        right: 20px; }
    @include rmin($desktop) {
        padding: 3px 20px 7px;
        bottom: 125px;
        right: -65px;
        transform: rotate(90deg);
        transform-origin: 50% 50%; } }



.btn--filter, .btn--categories {
    display: block;
    width: 40px;
    height: 40px;
    background: #999999;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    backface-visibility: hidden;
    .ico {
        transition: 0.3s $ease; }
    .ico-close {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) scale(0);
        z-index: 10;
        font-size: 16px;
        opacity: 0; }
    &:hover {
        background: $primaryColor; }
    @include rmin($tablet) {
        width: 60px;
        height: 60px;
        .ico-close {
            font-size: 24px; } } }


.btn--categories {
    position: absolute;
    top: -115px;
    right: -20px;
    z-index: 110;
    .ico-categories {
      display: none;
      font-size: 30px; }
    .ico-categories-mobile {
      display: inline-block;
      font-size: 20px; }

    @include rmin($tablet) {
        top: -165px;
        right: 0;
        .ico-categories {
          display: inline-block; }
        .ico-categories-mobile {
          display: none; } }
    @include rmin($desktop) {
        right: auto;
        top: 105px;
        left: 50%;
        margin: 0 0 0 -50vw; }

